.wp-block-pulsar-embla-carousel-progress {
	// stylelint-disable custom-property-pattern -- backwards compatibility
	--pb--embla-carousel--progress--background: var(--carousel-progress-background, var(--wp--custom--color--surface--inactive));
	--pb--embla-carousel--progress--bar: var(--carousel-progress-bar, var(--wp--custom--color--secondary--200));
	--pb--embla-carousel--progress--border-radius: var(--carousel-progress-border-radius, var(--wp--custom--border-radius--xl));
	--pb--embla-carousel--progress--height: var(--carousel-progress-height, 10px);
	// stylelint-enable custom-property-pattern

	background-color: var(--pb--embla-carousel--progress--background);
	border-radius: var(--pb--embla-carousel--progress--border-radius);
	height: var(--pb--embla-carousel--progress--height);
	overflow: hidden;
	position: relative;
	width: 100%;

	.embla__progress__bar {
		background-color: var(--pb--embla-carousel--progress--bar);
		border-radius: var(--pb--embla-carousel--progress--border-radius);
		bottom: 0;
		left: -100%;
		position: absolute;
		top: 0;
		transition: transform 200ms ease-out;
		width: 100%;
	}
}
